.el-scrollbar {
  overflow: hidden;
  position: relative;
}
.el-scrollbar:hover > .el-scrollbar__bar, .el-scrollbar:active > .el-scrollbar__bar, .el-scrollbar:focus > .el-scrollbar__bar {
  opacity: 1;
  transition: opacity 340ms ease-out;
}
.el-scrollbar__wrap {
  overflow: scroll;
  height: 100%;
}
.el-scrollbar__wrap--hidden-default {
  scrollbar-width: none;
}
.el-scrollbar__wrap--hidden-default::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.el-scrollbar__thumb {
  position: relative;
  display: block;
  width: 0;
  height: 0;
  cursor: pointer;
  border-radius: inherit;
  background-color: rgba(144, 147, 153, 0.3);
  transition: 0.3s background-color;
}
.el-scrollbar__thumb:hover {
  background-color: rgba(144, 147, 153, 0.5);
}

.el-scrollbar__bar {
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  border-radius: 4px;
  opacity: 0;
  transition: opacity 120ms ease-out;
}
.el-scrollbar__bar.is-vertical {
  width: 6px;
  top: 2px;
}
.el-scrollbar__bar.is-vertical > div {
  width: 100%;
}

.el-scrollbar__bar.is-horizontal {
  height: 6px;
  left: 2px;
}
.el-scrollbar__bar.is-horizontal > div {
  height: 100%;
}

.el-select {
  display: inline-block;
  position: relative;
}
.el-select .el-select__tags > span {
  display: contents;
}
.el-select:hover .el-input__inner {
  border-color: #C0C4CC;
}
.el-select .el-input__inner {
  cursor: pointer;
  padding-right: 35px;
}
.el-select .el-input__inner:focus {
  border-color: #5DBFD6;
}
.el-select .el-input .el-select__caret {
  color: #C0C4CC;
  font-size: 14px;
  transition: transform 0.3s;
  transform: rotateZ(180deg);
  cursor: pointer;
}
.el-select .el-input .el-select__caret.is-reverse {
  transform: rotateZ(0deg);
}

.el-select .el-input .el-select__caret.is-show-close {
  font-size: 14px;
  text-align: center;
  transform: rotateZ(180deg);
  border-radius: 100%;
  color: #C0C4CC;
  transition: color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.el-select .el-input .el-select__caret.is-show-close:hover {
  color: #909399;
}

.el-select .el-input.is-disabled .el-input__inner {
  cursor: not-allowed;
}
.el-select .el-input.is-disabled .el-input__inner:hover {
  border-color: #E4E7ED;
}
.el-select .el-input.is-focus .el-input__inner {
  border-color: #5DBFD6;
}
.el-select > .el-input {
  display: block;
}
.el-select__input {
  border: none;
  outline: none;
  padding: 0;
  margin-left: 15px;
  color: #666;
  font-size: 14px;
  appearance: none;
  height: 28px;
  background-color: transparent;
}
.el-select__input.is-mini {
  height: 14px;
}

.el-select__close {
  cursor: pointer;
  position: absolute;
  top: 8px;
  z-index: 1000;
  right: 25px;
  color: #C0C4CC;
  line-height: 18px;
  font-size: 14px;
}
.el-select__close:hover {
  color: #909399;
}

.el-select__tags {
  position: absolute;
  line-height: normal;
  white-space: normal;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.el-select .el-tag__close {
  margin-top: -2px;
}
.el-select .el-tag {
  box-sizing: border-box;
  border-color: transparent;
  margin: 2px 0 2px 6px;
  background-color: #f0f2f5;
}
.el-select .el-tag__close.el-icon-close {
  background-color: #C0C4CC;
  right: -7px;
  top: 0;
  color: #FFFFFF;
}
.el-select .el-tag__close.el-icon-close:hover {
  background-color: #909399;
}
.el-select .el-tag__close.el-icon-close::before {
  display: block;
  transform: translate(0, 0.5px);
}

.el-pagination {
  white-space: nowrap;
  padding: 2px 5px;
  color: #303133;
  font-weight: bold;
}
.el-pagination::before,
.el-pagination::after {
  display: table;
  content: "";
}

.el-pagination::after {
  clear: both;
}

.el-pagination span:not([class*=suffix]),
.el-pagination button {
  display: inline-block;
  font-size: 13px;
  min-width: 35.5px;
  height: 28px;
  line-height: 28px;
  vertical-align: top;
  box-sizing: border-box;
}
.el-pagination .el-input__inner {
  text-align: center;
  -moz-appearance: textfield;
  line-height: normal;
}
.el-pagination .el-input__suffix {
  right: 0;
  transform: scale(0.8);
}
.el-pagination .el-select .el-input {
  width: 100px;
  margin: 0 5px;
}
.el-pagination .el-select .el-input .el-input__inner {
  padding-right: 25px;
  border-radius: 3px;
}
.el-pagination button {
  border: none;
  padding: 0 6px;
  background: transparent;
}
.el-pagination button:focus {
  outline: none;
}
.el-pagination button:hover {
  color: #5DBFD6;
}
.el-pagination button:disabled {
  color: #C0C4CC;
  background-color: #FFFFFF;
  cursor: not-allowed;
}
.el-pagination .btn-prev,
.el-pagination .btn-next {
  background: center center no-repeat;
  background-size: 16px;
  background-color: #FFFFFF;
  cursor: pointer;
  margin: 0;
  color: #303133;
}
.el-pagination .btn-prev .el-icon,
.el-pagination .btn-next .el-icon {
  display: block;
  font-size: 12px;
  font-weight: bold;
}
.el-pagination .btn-prev {
  padding-right: 12px;
}
.el-pagination .btn-next {
  padding-left: 12px;
}
.el-pagination .el-pager li.disabled {
  color: #C0C4CC;
  cursor: not-allowed;
}
.el-pagination--small .btn-prev,
.el-pagination--small .btn-next,
.el-pagination--small .el-pager li,
.el-pagination--small .el-pager li.btn-quicknext,
.el-pagination--small .el-pager li.btn-quickprev,
.el-pagination--small .el-pager li:last-child {
  border-color: transparent;
  font-size: 12px;
  line-height: 22px;
  height: 22px;
  min-width: 22px;
}
.el-pagination--small .arrow.disabled {
  visibility: hidden;
}
.el-pagination--small .more::before,
.el-pagination--small li.more::before {
  line-height: 24px;
}
.el-pagination--small span:not([class*=suffix]),
.el-pagination--small button {
  height: 22px;
  line-height: 22px;
}
.el-pagination--small .el-pagination__editor {
  height: 22px;
}
.el-pagination--small .el-pagination__editor.el-input .el-input__inner {
  height: 22px;
}

.el-pagination__sizes {
  margin: 0 10px 0 0;
  font-weight: normal;
  color: #606266;
}
.el-pagination__sizes .el-input .el-input__inner {
  font-size: 13px;
  padding-left: 8px;
}
.el-pagination__sizes .el-input .el-input__inner:hover {
  border-color: #5DBFD6;
}

.el-pagination__total {
  margin-right: 10px;
  font-weight: normal;
  color: #606266;
}

.el-pagination__jump {
  margin-left: 24px;
  font-weight: normal;
  color: #606266;
}
.el-pagination__jump .el-input__inner {
  padding: 0 3px;
}

.el-pagination__rightwrapper {
  float: right;
}

.el-pagination__editor {
  line-height: 18px;
  padding: 0 2px;
  height: 28px;
  text-align: center;
  margin: 0 2px;
  box-sizing: border-box;
  border-radius: 3px;
}
.el-pagination__editor.el-input {
  width: 50px;
}
.el-pagination__editor.el-input .el-input__inner {
  height: 28px;
}
.el-pagination__editor .el-input__inner::-webkit-inner-spin-button,
.el-pagination__editor .el-input__inner::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.el-pagination.is-background .btn-prev,
.el-pagination.is-background .btn-next,
.el-pagination.is-background .el-pager li {
  margin: 0 5px;
  background-color: #f4f4f5;
  color: #606266;
  min-width: 30px;
  border-radius: 2px;
}
.el-pagination.is-background .btn-prev.disabled,
.el-pagination.is-background .btn-next.disabled,
.el-pagination.is-background .el-pager li.disabled {
  color: #C0C4CC;
}
.el-pagination.is-background .btn-prev, .el-pagination.is-background .btn-next {
  padding: 0;
}
.el-pagination.is-background .btn-prev:disabled, .el-pagination.is-background .btn-next:disabled {
  color: #C0C4CC;
}
.el-pagination.is-background .el-pager li:not(.disabled):hover {
  color: #5DBFD6;
}
.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #5DBFD6;
  color: #FFFFFF;
}
.el-pagination.is-background.el-pagination--small .btn-prev,
.el-pagination.is-background.el-pagination--small .btn-next,
.el-pagination.is-background.el-pagination--small .el-pager li {
  margin: 0 3px;
  min-width: 22px;
}

.el-pager {
  user-select: none;
  list-style: none;
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  padding: 0;
  margin: 0;
}
.el-pager .more::before {
  line-height: 30px;
}
.el-pager li {
  padding: 0 4px;
  background: #FFFFFF;
  vertical-align: top;
  display: inline-block;
  font-size: 13px;
  min-width: 35.5px;
  height: 28px;
  line-height: 28px;
  cursor: pointer;
  box-sizing: border-box;
  text-align: center;
  margin: 0;
}
.el-pager li.btn-quicknext, .el-pager li.btn-quickprev {
  line-height: 28px;
  color: #303133;
}
.el-pager li.btn-quicknext.disabled, .el-pager li.btn-quickprev.disabled {
  color: #C0C4CC;
}
.el-pager li.btn-quickprev:hover {
  cursor: pointer;
}
.el-pager li.btn-quicknext:hover {
  cursor: pointer;
}
.el-pager li.active + li {
  border-left: 0;
}
.el-pager li:hover {
  color: #5DBFD6;
}
.el-pager li.active {
  color: #5DBFD6;
  cursor: default;
}

