.el-date-table {
  font-size: 12px;
  user-select: none;
}
.el-date-table.is-week-mode .el-date-table__row:hover div {
  background-color: #F2F6FC;
}
.el-date-table.is-week-mode .el-date-table__row:hover td.available:hover {
  color: #606266;
}
.el-date-table.is-week-mode .el-date-table__row:hover td:first-child div {
  margin-left: 5px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}
.el-date-table.is-week-mode .el-date-table__row:hover td:last-child div {
  margin-right: 5px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}
.el-date-table.is-week-mode .el-date-table__row.current div {
  background-color: #F2F6FC;
}

.el-date-table td {
  width: 32px;
  height: 30px;
  padding: 4px 0;
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
  position: relative;
}
.el-date-table td div {
  height: 30px;
  padding: 3px 0;
  box-sizing: border-box;
}
.el-date-table td span {
  width: 24px;
  height: 24px;
  display: block;
  margin: 0 auto;
  line-height: 24px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
}
.el-date-table td.next-month, .el-date-table td.prev-month {
  color: #C0C4CC;
}
.el-date-table td.today {
  position: relative;
}
.el-date-table td.today span {
  color: #5DBFD6;
  font-weight: bold;
}
.el-date-table td.today.start-date span, .el-date-table td.today.end-date span {
  color: #FFFFFF;
}
.el-date-table td.available:hover {
  color: #5DBFD6;
}
.el-date-table td.in-range div {
  background-color: #F2F6FC;
}
.el-date-table td.in-range div:hover {
  background-color: #F2F6FC;
}
.el-date-table td.current:not(.disabled) span {
  color: #FFFFFF;
  background-color: #5DBFD6;
}
.el-date-table td.start-date div, .el-date-table td.end-date div {
  color: #FFFFFF;
}
.el-date-table td.start-date span, .el-date-table td.end-date span {
  background-color: #5DBFD6;
}
.el-date-table td.start-date div {
  margin-left: 5px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}
.el-date-table td.end-date div {
  margin-right: 5px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}
.el-date-table td.disabled div {
  background-color: #F5F7FA;
  opacity: 1;
  cursor: not-allowed;
  color: #C0C4CC;
}
.el-date-table td.selected div {
  margin-left: 5px;
  margin-right: 5px;
  background-color: #F2F6FC;
  border-radius: 15px;
}
.el-date-table td.selected div:hover {
  background-color: #F2F6FC;
}
.el-date-table td.selected span {
  background-color: #5DBFD6;
  color: #FFFFFF;
  border-radius: 15px;
}
.el-date-table td.week {
  font-size: 80%;
  color: #606266;
}
.el-date-table th {
  padding: 5px;
  color: #606266;
  font-weight: 400;
  border-bottom: solid 1px #EBEEF5;
}

