.el-scrollbar {
  overflow: hidden;
  position: relative;
}
.el-scrollbar:hover > .el-scrollbar__bar, .el-scrollbar:active > .el-scrollbar__bar, .el-scrollbar:focus > .el-scrollbar__bar {
  opacity: 1;
  transition: opacity 340ms ease-out;
}
.el-scrollbar__wrap {
  overflow: scroll;
  height: 100%;
}
.el-scrollbar__wrap--hidden-default {
  scrollbar-width: none;
}
.el-scrollbar__wrap--hidden-default::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.el-scrollbar__thumb {
  position: relative;
  display: block;
  width: 0;
  height: 0;
  cursor: pointer;
  border-radius: inherit;
  background-color: rgba(144, 147, 153, 0.3);
  transition: 0.3s background-color;
}
.el-scrollbar__thumb:hover {
  background-color: rgba(144, 147, 153, 0.5);
}

.el-scrollbar__bar {
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  border-radius: 4px;
  opacity: 0;
  transition: opacity 120ms ease-out;
}
.el-scrollbar__bar.is-vertical {
  width: 6px;
  top: 2px;
}
.el-scrollbar__bar.is-vertical > div {
  width: 100%;
}

.el-scrollbar__bar.is-horizontal {
  height: 6px;
  left: 2px;
}
.el-scrollbar__bar.is-horizontal > div {
  height: 100%;
}

.el-cascader-panel {
  display: flex;
  border-radius: 0;
  font-size: 14px;
}
.el-cascader-panel.is-bordered {
  border: solid 1px #E4E7ED;
  border-radius: 0;
}

.el-cascader-menu {
  min-width: 180px;
  box-sizing: border-box;
  color: #606266;
  border-right: solid 1px #E4E7ED;
}
.el-cascader-menu:last-child {
  border-right: none;
}
.el-cascader-menu:last-child .el-cascader-node {
  padding-right: 20px;
}
.el-cascader-menu__wrap {
  height: 204px;
}

.el-cascader-menu__list {
  position: relative;
  min-height: 100%;
  margin: 0;
  padding: 6px 0;
  list-style: none;
  box-sizing: border-box;
}

.el-cascader-menu__hover-zone {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.el-cascader-menu__empty-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #C0C4CC;
}

.el-cascader-node {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 30px 0 20px;
  height: 34px;
  line-height: 34px;
  outline: none;
}
.el-cascader-node.is-selectable.in-active-path {
  color: #606266;
}
.el-cascader-node.in-active-path, .el-cascader-node.is-selectable.in-checked-path, .el-cascader-node.is-active {
  color: #5DBFD6;
  font-weight: bold;
}
.el-cascader-node:not(.is-disabled) {
  cursor: pointer;
}
.el-cascader-node:not(.is-disabled):hover, .el-cascader-node:not(.is-disabled):focus {
  background: #F5F7FA;
}
.el-cascader-node.is-disabled {
  color: #C0C4CC;
  cursor: not-allowed;
}

.el-cascader-node__prefix {
  position: absolute;
  left: 10px;
}

.el-cascader-node__postfix {
  position: absolute;
  right: 10px;
}

.el-cascader-node__label {
  flex: 1;
  padding: 0 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.el-cascader-node > .el-radio {
  margin-right: 0;
}
.el-cascader-node > .el-radio .el-radio__label {
  padding-left: 0;
}

