.el-upload {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  outline: none;
  /* 照片墙模式 */
}
.el-upload__input {
  display: none;
}

.el-upload__tip {
  font-size: 12px;
  color: #606266;
  margin-top: 7px;
}

.el-upload iframe {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  opacity: 0;
  filter: alpha(opacity=0);
}
.el-upload--picture-card {
  background-color: #fbfdff;
  border: 1px dashed #c0ccda;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 148px;
  height: 148px;
  cursor: pointer;
  line-height: 146px;
  vertical-align: top;
}
.el-upload--picture-card i {
  font-size: 28px;
  color: #8c939d;
}
.el-upload--picture-card:hover {
  border-color: #5DBFD6;
  color: #5DBFD6;
}

.el-upload:focus {
  border-color: #5DBFD6;
  color: #5DBFD6;
}
.el-upload:focus .el-upload-dragger {
  border-color: #5DBFD6;
}

.el-upload-dragger {
  background-color: #fff;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 360px;
  height: 180px;
  text-align: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.el-upload-dragger .el-icon-upload {
  font-size: 67px;
  color: #C0C4CC;
  margin: 40px 0 16px;
  line-height: 50px;
}
.el-upload-dragger + .el-upload__tip {
  text-align: center;
}
.el-upload-dragger ~ .el-upload__files {
  border-top: 1px solid #DCDFE6;
  margin-top: 7px;
  padding-top: 5px;
}
.el-upload-dragger .el-upload__text {
  color: #606266;
  font-size: 14px;
  text-align: center;
}
.el-upload-dragger .el-upload__text em {
  color: #5DBFD6;
  font-style: normal;
}
.el-upload-dragger:hover {
  border-color: #5DBFD6;
}
.el-upload-dragger.is-dragover {
  background-color: rgba(32, 159, 255, 0.06);
  border: 2px dashed #5DBFD6;
}

.el-upload-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.el-upload-list__item {
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  font-size: 14px;
  color: #606266;
  line-height: 1.8;
  margin-top: 5px;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 4px;
  width: 100%;
}
.el-upload-list__item .el-progress {
  position: absolute;
  top: 20px;
  width: 100%;
}
.el-upload-list__item .el-progress__text {
  position: absolute;
  right: 0;
  top: -13px;
}
.el-upload-list__item .el-progress-bar {
  margin-right: 0;
  padding-right: 0;
}
.el-upload-list__item:first-child {
  margin-top: 10px;
}
.el-upload-list__item .el-icon-upload-success {
  color: #67C23A;
}
.el-upload-list__item .el-icon-close {
  display: none;
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
  opacity: 0.75;
  color: #606266;
}
.el-upload-list__item .el-icon-close:hover {
  opacity: 1;
}
.el-upload-list__item .el-icon-close-tip {
  display: none;
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 12px;
  cursor: pointer;
  opacity: 1;
  color: #5DBFD6;
}
.el-upload-list__item:hover {
  background-color: #F5F7FA;
}
.el-upload-list__item:hover .el-icon-close {
  display: inline-block;
}
.el-upload-list__item:hover .el-progress__text {
  display: none;
}
.el-upload-list__item.is-success .el-upload-list__item-status-label {
  display: block;
}
.el-upload-list__item.is-success .el-upload-list__item-name:hover, .el-upload-list__item.is-success .el-upload-list__item-name:focus {
  color: #5DBFD6;
  cursor: pointer;
}
.el-upload-list__item.is-success:focus:not(:hover) {
  /* 键盘focus */
}
.el-upload-list__item.is-success:focus:not(:hover) .el-icon-close-tip {
  display: inline-block;
}
.el-upload-list__item.is-success:not(.focusing):focus, .el-upload-list__item.is-success:active {
  /* click时 */
  outline-width: 0;
}
.el-upload-list__item.is-success:not(.focusing):focus .el-icon-close-tip, .el-upload-list__item.is-success:active .el-icon-close-tip {
  display: none;
}
.el-upload-list__item.is-success:hover .el-upload-list__item-status-label, .el-upload-list__item.is-success:focus .el-upload-list__item-status-label {
  display: none;
}

.el-upload-list.is-disabled .el-upload-list__item:hover .el-upload-list__item-status-label {
  display: block;
}

.el-upload-list__item-name {
  color: #606266;
  display: block;
  margin-right: 40px;
  overflow: hidden;
  padding-left: 4px;
  text-overflow: ellipsis;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  white-space: nowrap;
}
.el-upload-list__item-name [class^=el-icon] {
  height: 100%;
  margin-right: 7px;
  color: #909399;
  line-height: inherit;
}

.el-upload-list__item-status-label {
  position: absolute;
  right: 5px;
  top: 0;
  line-height: inherit;
  display: none;
}

.el-upload-list__item-delete {
  position: absolute;
  right: 10px;
  top: 0;
  font-size: 12px;
  color: #606266;
  display: none;
}
.el-upload-list__item-delete:hover {
  color: #5DBFD6;
}

.el-upload-list--picture-card {
  margin: 0;
  display: inline;
